* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

html {
	font-family: sans-serif;
	color: #212121;
}

.container {
	max-width: 800px;
	margin-left: auto;
	margin-right: auto;
}

.app-component {
	position: fixed;
	left: 200px;
	top: 0;
	bottom: 0;
	right: 0;
	background: #ededed;
}

.app-sidebar {
	position: absolute;
	z-index: 20;
	right: 100%;
	top: 0;
	bottom: 0;
	width: 200px;
	background: white;
	-webkit-box-shadow: 1px 2px 3px rgba(0,0,0,.4);
	        box-shadow: 1px 2px 3px rgba(0,0,0,.4);
	-webkit-transition: -webkit-transform .3s ease-out;
	transition: -webkit-transform .3s ease-out;
	-o-transition: transform .3s ease-out;
	transition: transform .3s ease-out;
	transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}

.sidebar-header {
	padding: 10px;
	background: #9a1b38;
	color: #fff;
	-webkit-box-shadow: 1px 2px 3px rgba(0,0,0,.4);
	        box-shadow: 1px 2px 3px rgba(0,0,0,.4);
}

.sidebar-header a {
	display: block;
	padding: 5px 10px;
	color: #f7cba3;
	text-decoration: none;
}

.sidebar-header a:hover,
.sidebar-header a:active {
	background: #d3253c;
	color: white;
}

.sidebar-search {
	width: 100%;
	padding: 10px;
	margin: 10px 0 0;
	border: 1px solid #5b0833;
	border-radius: 2px;
}

.notes-list-component {
	padding: 0;
	margin: 0;
	list-style-type: none;
}

.note-teaser {
	display: block;
	padding: 10px 20px;
	border-bottom: 1px solid #BDBDBD;
}

.note-teaser:hover,
.note-teaser:active {
	background: #f7cba3;
	color: #ee5f49;
}

.note-teaser.active {
	color: #9a1b38;
}

a.note-teaser {
	color: #757575;
	text-decoration: none;
}

.app-header {
	position: absolute;
	z-index: 10;
	top: 0;
	left: 0;
	right: 0;
	padding: 10px 0;
	background: #5b0833;
	color: #f7cba3;
	-webkit-box-shadow: 1px 2px 3px rgba(0,0,0,.4);
	        box-shadow: 1px 2px 3px rgba(0,0,0,.4);
}

#sidebarControl {
	display: none;
}

.app-title {
	font-size: 20px;
	margin: 0 20px;
	line-height: 1em;
}

.app-main {
	max-height: 100%;
	padding: 60px 20px 20px;
	overflow-y: auto;
}

.card {
	position: relative;
	padding: 20px;
	border-radius: 3px;
	background: white;
	-webkit-box-shadow: 1px 2px 3px rgba(0,0,0,0.4);
	        box-shadow: 1px 2px 3px rgba(0,0,0,0.4);
	overflow: hidden;
}

.card-header {
	margin-top: 0;
	margin: -20px -20px 20px;
	padding: 10px 20px;
	border-bottom: 1px solid #ddd;
	color: #9a1b38;
}

.card-footer {
	border-top: 1px solid #ddd;
	margin: 0 -20px -20px;
	padding: 10px 20px;
	text-align: right;
}

button {
	border: 0 none;
	padding: 10px 20px;
	border-radius: 3px;
	background: #888;
	color: white;
	-webkit-box-shadow: inset -1px -1px 10px rgba(0,0,0,.1);
	        box-shadow: inset -1px -1px 10px rgba(0,0,0,.1);
	cursor: pointer;
}

button:disabled {
	opacity: .7;
	text-decoration: line-through;
}

button.edit,
button.save {
	background: #d3253c;
}

.note-component .note-content {
	white-space: pre-wrap;
}

.note-action {
	margin-left: 10px;
}

.form-field {
	display: block;
	width: 100%;
	padding: 10px;
	margin: 0 0 20px;
	border: 1px solid #BDBDBD;
	border-radius: 2px;
}

.form-field.note-content {
	height: 400px;
}

.app-component:before {
	content: 'Loading ...';
	position: fixed;
	z-index: 20;
	top: 100px;
	left: 50%;
	width: 200px;
	height: 100px;
	margin: 0 0 0 -100px;
	background: #5b0833;
	color: #f7cba3;
	font-weight: bold;
	text-align: center;
	line-height: 100px;
	-webkit-transform: translateY(-200px);
	    -ms-transform: translateY(-200px);
	        transform: translateY(-200px);
	opacity: 0;
	-webkit-transition: .2s ease-in;
	-o-transition: .2s ease-in;
	transition: .2s ease-in;
	-webkit-animation: curtain .2s 1 ease-in;
	        animation: curtain .2s 1 ease-in;
	-webkit-box-shadow: 1px 2px 3px rgba(0,0,0,.4);
	        box-shadow: 1px 2px 3px rgba(0,0,0,.4);
	border-radius: 3px;
}

.app-component.loading:before {
	-webkit-transform: translateY(0);
	    -ms-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
}

.app-component:after {
	content: '';
	cursor: progress;
	position: fixed;
	z-index: 10;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0,0,0,.4);
	-webkit-transform: translateY(100%);
	    -ms-transform: translateY(100%);
	        transform: translateY(100%);
	opacity: 0;
	-webkit-transition: opacity .2s ease-in;
	-o-transition: opacity .2s ease-in;
	transition: opacity .2s ease-in;
}

.app-component.loading:after {
	-webkit-transform: translateY(0);
	    -ms-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
}

@-webkit-keyframes curtain {
	0% {
		-webkit-transform: translateY(-50%);
		        transform: translateY(-50%);
		opacity: 0;
	}
	100% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
		opacity: 1;
	}
}

@keyframes curtain {
	0% {
		-webkit-transform: translateY(-50%);
		        transform: translateY(-50%);
		opacity: 0;
	}
	100% {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
		opacity: 1;
	}
}

.tile {
	position: relative;
	display: block;
	margin: 10px 0;
	padding: 10px 15px;
	border: 1px solid #ddd;
	border-radius: 3px;
	overflow: hidden;
	background: #fafafa;
}

a.tile,
a.tile:visited {
	color: inherit;
	text-decoration: none;
}

.tile:after {
	content: '';
	position: absolute;
	top: 5px;
	right: 5px;
	width: 12px;
	height: 12px;
	background: url("external.png") no-repeat 50% 50%;
	background-size: 100% auto;
}

.profile-tile .profile-image {
	float: left;
	width: 32px;
	height: 32px;
	margin-right: 10px;
	border-radius: 100%;
}

.profile-tile .profile-name {
	font-weight: bold;
}

.profile-tile .profile-email {
	font-size: 0.8em;
	opacity: 0.8;
}

.document-tile:before {
	content: '';
	float: left;
	width: 32px;
	height: 32px;
	margin-right: 10px;
	background: url("document.png") no-repeat 50% 50%;
	background-size: 100% auto;
}

.document-tile .document-title {
	font-weight: bold;
	font-size: 16px;
	line-height: 32px;
}

@media screen and (max-width: 800px) {
	.app-component {
		left: 0;
	}

	.app-main {
		padding: 40px 0 20px;
	}

	.app-sidebar {
		-webkit-transform: translateX(-10px);
		    -ms-transform: translateX(-10px);
		        transform: translateX(-10px);
	}

	.sidebar-toggle {
		position: relative;
		width: 20px;
		height: 16px;
		float: left;
		margin: 2px 20px;
		color: #f7cba3;
		border-top: 3px solid;
		border-bottom: 3px solid;
		border-radius: 2px;
	}

	.sidebar-toggle:before {
		content: '';
		position: absolute;
		top: 4px;
		left: 1px;
		right: 1px;
		border-top: 2px solid;
	}

	.sidebar-toggle:after {
		content: '';
		position: absolute;
		top: -15px;
		left: -15px;
		bottom: -15px;
		right: -15px;
	}

	#sidebarControl:checked ~ .app-sidebar {
		-webkit-transform: translateX(200px);
		    -ms-transform: translateX(200px);
		        transform: translateX(200px);
	}

	#sidebarControl:checked ~ .sidebar-overlay {
		position: absolute;
		z-index: 10;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(0,0,0,.4);
	}
}


